            <div class="cart-list">
              <div class="thead">
                <div class="box">
                  <div class="th th-title">商品名稱</div>
                  <div class="th th-price">售價</div>
                  <div class="th th-number">數量</div>
                  <div class="th th-sum">小計</div>
                  <div class="th th-del">刪除</div>
                </div>
              </div>
              <div class="tbody">
                <!-- 單筆開始 -->
                <div class="box" v-for="item in cart.carts" :key="item.id">
                  <div class="td td-title">
                    <div class="pic">
                      <img :src="item.product.imageUrl" alt="product">
                    </div>
                    <div class="txt">
                      <div class="prod-name">{{ item.product.title }}</div>
                    </div>
                  </div>
                  <div class="td td-price" data-mtit="單價">
                    <span class="origin">NT${{ item.product.price }}</span>
                  </div>
                  <div class="td td-number" data-mtit="數量">{{ item.qty }}</div>
                  <div class="td td-sum" data-mtit="小計">NT${{ item.total }}</div>
                  <div class="td td-del"><i class="bi bi-x-circle" @click="deleteCart(item.id)"></i></div>
                </div>
                <!-- 結尾 -->
              </div>
              <div class="tfooter">
                <div class="info">
                  <ul class="reset">
                    <li>
                      <div class="txt">購買總課程堂數</div>
                      <div class="num">{{ courseTotal }}堂</div>
                    </li>
                    <li class="total">
                      <div class="txt">訂單總金額</div>
                      <div class="num">NT${{ total }}</div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </template>
          <div class="btn-wrap d-flex"><router-link to="/cartForm" class="btn-style" title="下一步"><span>下一步</span><i class="bi bi-arrow-right"></i></router-link></div>
        </div>
      </div>
